.Calendar {
  width: 100%;
  height: 100vh;
  padding-bottom: 200px;
  overflow-x: hidden;
}

.Calendar .title {
  text-align: start;
  font-size: 16px;
  font-weight: 400;
  color: var(--black);
  margin: 10px 0;
  text-transform: capitalize;
}

.Calendar table {
  width: 100%;
  border-collapse: collapse;
}

.Calendar th {
  height: 40px;
}
.Calendar td {
  width: 50px;
  height: 50px;
  text-align: center;
  cursor: pointer;
}

.Calendar .inactive {
  color: #ccc !important;
  cursor: not-allowed !important;
  background: none !important;
}

.Calendar .range.inactive.not-in-data {
  color: #ccc !important;
  cursor: not-allowed !important;
  background: none !important;
}

.Calendar .selected-range-start.range.inactive.not-in-data {
  color: #ccc !important;
  cursor: not-allowed !important;
  background: none !important;
}

.Calendar .not-in-data {
  font-weight: 400;
  color: #606060;
  cursor: not-allowed;
  background: none;
}

.Calendar .price_date {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
  font-weight: 300;
  font-size: 11px;
  color: #606060;
}

.Calendar .day-name {
  font-size: 14;
  font-weight: 500;
}

.Calendar td.disabled {
  color: #ccc !important;
  cursor: not-allowed !important;
  background: none !important;
}

.Calendar .selected-range-start {
  border-radius: 5px;
  color: var(--white) !important;
  background: var(--blue) !important;
}

.Calendar .selected-range-start .price_date {
  color: var(--white) !important;
}

.Calendar .selected-range-start.inactive.not-in-data {
  color: #ccc !important;
  cursor: not-allowed !important;
  background: none !important;
}

.Calendar .selected {
  background: #f3f3f3;
}
