.register {
  min-height: 0px;
  padding-bottom: 120px;
}

.register .register_from {
  padding-top: 12px;
  margin: 0 auto;
  max-width: 500px;
  width: 100%;
}

.register .input_register .PhoneInputCountrySelect {
  position: absolute;
  z-index: 3;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 40px;
  height: 40px;
  border: none;
  outline: none;
  color: transparent;
  background: transparent !important;
  appearance: none !important;
  -webkit-appearance: none !important;
}

.register .input_register .PhoneInputCountrySelect::after {
  content: '▼';
  font-size: 15px;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
}

.register .input_register .PhoneInputInput {
  margin-top: 2px;
  width: 100%;
  height: 40px;
  background: #fdfdfd;
  border: none;
  outline: none;
  font-size: 14px;
  font-weight: 400;
  color: #081f2c;
  border-bottom: 1px solid #377aff94;
  padding-left: 40px;
}

.register .input_mask {
  top: 21px;
  position: absolute;
  height: 40px;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px;
}

.register .select {
  top: 61px;
  left: 0;
  position: absolute;
  min-width: 40px;
  max-height: 250px;
  overflow-x: hidden;
  padding: 8px 3px;
  z-index: 9999;
  background: #fdfdfd;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
}

.register .option {
  padding: 3px 8px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.register .input_mask img,
.register .option img {
  width: 20px;
  border-radius: 2px;
  object-fit: cover;
  object-position: center;
}

.register .option p,
.register .input_mask p {
  font-size: 15px;
  font-weight: 400;
  color: var(--black);
}

.register .top {
  margin-top: 30px;
}

.register .p_content {
  font-size: 14px;
  font-weight: 400;
  color: var(--black);
  text-align: center;
  line-height: 1;
}

.register .a_content {
  color: var(--blue);
  font-size: 15px;
  font-weight: 500;
}

.register .red {
  text-align: end;
  font-weight: 400;
  font-size: 13px;
  color: red;
}

.button_form {
  margin: 12px 0;
}

.input_register.PhoneInput {
  position: relative;
}

.PhoneInputCountry {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 40px;
  height: 40px;
}

.PhoneInputCountryIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 40px;
  height: 40px;
  margin: auto;
  z-index: 1;
}

.register .input_register img {
  width: 25px;
}