.payment_code {
  width: 100%;
  height: 100vh;
}

.payment_code .center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.payment_code .image_payment {
  margin: 20px auto;
  width: 80px;
  height: 80px;
  border-radius: 10px;
  object-fit: cover;
  object-position: center;
}

.payment_code h2 {
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  color: var(--black);
}

.payment_code .label_form {
  margin: 20px 0;
  text-align: center;
}

.payment_code .inputs {
  display: grid;
  justify-content: center;
  grid-template-columns: 50px 50px 50px 50px;
  gap: 10px;
  margin-bottom: 30px;
}

.payment_code .inputs .input_form {
  text-align: center;
  font-size: 17px;
}

.payment_code .input_form.error {
  background: rgba(255, 0, 0, 0.15) !important;
  border: 1px solid red !important;
}

.payment_code .input_form.error:focus {
  border: 1px solid red !important;
}
