.specific {
  width: 100%;
}
.specific .nav_block {
  padding-top: 15px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.specific .nav_block img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
}
.specific .nav_block h2 {
  color: var(--black);
  font-size: 16px;
  font-weight: 500;
  margin: 5px 0;
}
.specific .nav_block p {
  color: var(--black);
  font-size: 14px;
  font-weight: 400;
}
.specific .nav_block a {
  color: var(--blue);
  font-size: 14px;
  font-weight: 400;
  margin: 0;
}
.specific .img_save {
  position: relative;
}
.specific .img_save .icon {
  right: 0;
  bottom: 0;
  cursor: pointer;
  position: absolute;
  color: var(--blue);
}
.specific .input_save {
  margin-top: 30px;
}
.specific .not {
  cursor: not-allowed;
}
.specific .photo_big {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: hsla(0, 0%, 0%, 0.4);
  z-index: 99999;
}
.specific .photo_big img {
  position: absolute;
  width: 100%;
  max-height: 50vh;
  object-fit: cover;
  object-position: center;
  z-index: 1;
}
