.compilation {
  margin: 0 auto; 
  width: 100%;
  padding-bottom: 120px;
}

.compilation .tour {
  margin-top: 20px;
  width: 100%;
  min-height: 200px;
}

.compilation .tour h1 {
  margin-left: 5%;
  color: #212529;
  font-size: 18px;
  font-weight: 600;
}

.compilation .map_boxs {
  width: 90%;
  display: grid;
  grid-gap: 5px;
  grid-template-columns: 1fr 1fr;
  margin: 20px auto;
}

.compilation .map_boxs::-webkit-scrollbar {
  display: none;
}

.compilation .compilation_box {
  float: left;
  width: 100%;
  height: 110px;
  border-radius: 4px;
  background: var(--white);
  position: relative;
  overflow: hidden;
}

.compilation .compilation_box img {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.compilation .compilation_box h1 {
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  margin-left: 0 !important;
}

.compilation .compilation_box p {
  color: #fff;
  font-size: 14px;
  font-weight: 300;
}

.compilation .tour .btn {
  display: flex;
  margin: 0 auto;
  margin-top: 15px;
  padding: 5px 12px;
  border-radius: 7px;
  background: var(--blue);
  color: var(--unnamed, #fdfdfd);
  font-size: 14px;
  font-weight: 400;
  border: none;
}

.compilation .like p {
  font-size: 14px;
  font-weight: 400;
}

.compilation .old {
  display: none;
  font-size: 12px;
  text-decoration: line-through;
}

.tour-info-wrap {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.65) 79%,rgba(0,0,0,0.65) 100%);
  padding: 10px;
}