.filter {
  position: fixed;
  bottom: 15px;
  right: 5%;
  left: 50%;
  transform: translateX(-50%);
  width: 94%;
  border-radius: 5px;
  color: var(--white);
  font-size: 16px;
  font-weight: 500;
  display: flex;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr;
  cursor: pointer;
  /* background: var(--white);
    border: 1px solid rgba(0, 0, 0, 0.2); */
  z-index: 10;
}

.filter .filter_btn {
  margin: 0 auto;
  width: 100%;
  height: 55px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  background: #73b3ee;
  justify-content: center;
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.3) !important;
  position: relative;
}

.filter .filter_btn .after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 55px;
  background: var(--blue);
  border-radius: 5px;
}

.filter .filter_btn .clickable {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 55px;
  z-index: 15;
}

@keyframes WidthP {
  0% {
    width: 1%;
    background: #4ea0ed86;
  }

  100% {
    width: 100%;
    background: var(--blue);
  }
}

