.decor_hotel {
  width: 100%;
  padding-bottom: 120px;
}

.decor_hotel .formm {
  padding-top: 12px;
}

.decor_hotel .hotel {
  width: 100%;
}

.decor_hotel .hotel_flex .image {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
}

.decor_hotel .tour_block {
  margin-top: 12px;
  width: 100%;
  min-height: 10px;
  border-radius: 10px;
  padding: 12px;
  border: 1px solid var(--gray);
}

.decor_hotel .flex {
  width: 100%;
  display: grid;
  grid-template-columns: 2.5fr 1fr;
}

.decor_hotel .block {
  display: flex;
  align-items: center;
  gap: 10px;
}

.decor_hotel .image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
}

.decor_hotel .tour_block h2 {
  color: #081f2c;
  font-size: 15px;
  font-weight: 500;
}

.decor_hotel .tour_block p {
  color: #888;
  font-size: 14px;
  font-weight: 400;
}

.decor_hotel .tour_block .hr {
  width: 100%;
  height: 1px;
  background: var(--gray);
  margin: 15px 0;
}

.decor_hotel .description {
  overflow: hidden;
  height: 41px;
  line-height: 1;
  color: #081f2c !important;
  font-size: 14px;
  font-weight: 400;
}

.decor_hotel .next {
  margin-top: 10px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}

.decor_hotel .flex .icons_flex {
  display: flex;
  justify-content: end;
}

.decor_hotel .tour_block .icon {
  color: gold;
  margin-right: 3px;
}

.decor_hotel h3 {
  font-size: 16px;
  font-weight: 400;
  color: var(--black);
}

.decor_hotel .services li {
  margin-left: 10px;
}

.decor_hotel .blocks {
  display: flex;
  gap: 12px;
}

.decor_hotel .icons {
  font-size: 20px;
  color: var(--blue);
}
