.modal_home {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgb(0, 0, 0, 0.5);
  z-index: 999;
  animation: modalOpen 0.3s ease;
}

@keyframes modalOpen {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.modal_home .not_modal {
  width: 100%;
  height: 100vh;
  position: relative;
}

.modal_home .modal {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background: #fff;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  z-index: 1;
  animation: modalTop 0.7s ease;
}

.modal_home .modal .box_header {
  margin-bottom: 15px;
}

@keyframes modalTop {
  0% {
    opacity: 0;
    right: -500px;
  }

  100% {
    opacity: 1;
    right: 0;
  }
}

.modal_home .padding::-webkit-scrollbar {
  display: block;
}

.modal_home .padding {
  background: transparent;
}
