.payment_mbank {
  width: 100%;
  height: 100vh;
}

.payment_mbank .center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.payment_mbank .image_payment {
  margin: 20px auto;
  width: 80px;
  height: 80px;
  border-radius: 10px;
  object-fit: cover;
  object-position: center;
}

.payment_mbank h2 {
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  color: var(--black);
}

.payment_mbank .input_box {
  margin-top: 30px;
  height: 80px;
}

.payment_mbank .input_form.error {
  background: rgba(255, 0, 0, 0.15) !important;
  border: 1px solid red !important;
}

.payment_mbank .input_form.error:focus {
  border: 1px solid red !important;
}

.payment_mbank .red {
  text-align: end;
  font-size: 14px;
  font-weight: 400;
  color: red;
}

.payment_mbank .input_box .input_form.pay {
  padding-left: 45px;
}

.payment_mbank .relative_pay {
  position: relative;
}

.payment_mbank .absolute_pay {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  left: 15px;
  display: flex;
  align-items: center;
}

.payment_mbank .warning {
  margin-top: 5px;
  width: 100%;
  padding: 16px;
  border-radius: 3px;
  background: rgba(255, 200, 0, 0.25);
  display: flex;
  align-items: center;
  gap: 15px;
}

.payment_mbank .warning .icon {
  color: rgb(255, 179, 0);
}

.payment_mbank .warning p {
  font-size: 15px;
  font-weight: 400;
  color: var(--black);
}

.payment_mbank .sum {
  margin: 30px 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  font-weight: 400;
  color: var(--black);
}

.payment_mbank .sum .strong {
  margin-left: 10px;
  font-weight: 500;
  font-size: 16px;
}
