.contact {
  background-color: #fff;
  width: 100%;
  min-height: 800px;
  padding-bottom: 120px;
}
.contact .contacts_all .faq {
  display: flex;
  align-items: center;
  cursor: pointer;
  border-radius: 10px;
  padding: 8px;
  background: #fdfdfd;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.05);
}
.contact .contacts_all .faq .blue {
  margin-left: 8px;
  font-size: 14px;
  font-weight: 500;
  color: var(--black);
}
.contact .contacts_all .faq .icon {
  color: var(--blue);
  margin-left: auto;
}
.contact img {
  display: flex;
  margin: 35px auto;
  width: 35%;
}
.contact .title {
  text-align: center;
  font-size: 35px;
  font-weight: 100;
  color: #cdcdcd;
}
.contact .box.d {
  margin-top: 25px;
}
.contact .box {
  display: flex;
  align-items: center;
  padding: 10px;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
}
.contact .icon {
  color: var(--blue);
  font-size: 22px;
  margin-right: 15px;
}
.contact a,
.contact h3 {
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  color: var(--black);
  line-height: 1;
}
.contact p {
  font-size: 14px;
  font-weight: 300;
  color: #b8b8b8;
  line-height: 1;
}
.contact .modal_div {
  border-top: 1px solid rgba(0, 0, 0, 0.05);
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px;
}
.contact .location h1 {
  font-size: 16px;
  font-weight: 500;
  color: var(--black);
  margin-bottom: 8px;
}
.contact .box.d.svg {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 8px;
  padding: 8px 0;
}
.contact h2 {
  width: 100%;
  font-size: 15px;
  font-weight: 500;
  color: var(--black);
}
.contact .flex {
  display: flex;
  gap: 8px;
}
.contact .svg_div {
  width: 60px;
  height: 60px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.05);
}
.contact .svg_div img {
  width: 30px;
  height: 30px;
}
