.preloader-container {
  margin-top: 200px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.plan {
  transform: rotate(-45deg) !important;
}

.airplane {
  font-size: 50px;
  position: absolute;
  animation: approach-and-withdraw 5s ease-in-out infinite,
    rotate-around 5s linear infinite !important;
  transform-origin: center center;
  /* Initial orientation of the airplane, adjust this based on the airplane emoji's orientation */
  transform: rotate(
    -45deg
  ); /* Adjust based on how you want the airplane to face */
}

.loading-text {
  font-size: 24px;
  color: #333;
  position: relative;
  z-index: 1; /* Ensure text is above airplane */
}

.dot {
  font-size: 30px;
  animation: blink 1.2s infinite;
}

.dot:nth-child(1) {
  animation-delay: 0s;
}

.dot:nth-child(2) {
  animation-delay: 0.4s;
}

.dot:nth-child(3) {
  animation-delay: 0.8s;
}

/* Ключевые кадры для приближения и отдаления */
@keyframes approach-and-withdraw {
  0% {
    transform: translateY(-200px) scale(1) rotate(-45deg); /* Начать далеко сверху и в обычном размере */
    opacity: 0.5;
  }
  50% {
    transform: translateY(-150px) scale(1.2) rotate(-45deg); /* Подойти ближе к тексту и увеличить размер */
    opacity: 1;
  }
  100% {
    transform: translateY(-200px) scale(1) rotate(-45deg); /* Вернуться в исходное положение и размер */
    opacity: 0.5;
  }
}

/* Ключевые кадры для вращения вокруг текста */
@keyframes rotate-around {
  0% {
    transform: translateX(0) rotate(0deg) translateX(-110px); /* Начало вращения */
  }
  100% {
    transform: translateX(0) rotate(360deg) translateX(-110px); /* Полный оборот вокруг текста */
  }
}

@keyframes blink {
  0%,
  100% {
    opacity: 0.3;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.5);
  }
}
