.cabinet {
  width: 100%;
  background: #fff;
  padding-bottom: 120px;
}
.cabinet .cabinet_header {
  width: 100%;
  min-height: 50px;
  background: var(--blue);
  padding-bottom: 20px;
}
.cabinet .head_tour {
  display: flex;
  align-items: center;
  height: 100px;
  gap: 10px;
  overflow: hidden;
}
.cabinet .head_tour img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
  margin-right: 8px;
}
.cabinet .head_tour h1 {
  color: var(--white);
  font-size: 15px;
  font-weight: 500;
}
.cabinet .head_tour p {
  margin-top: 3px;
  color: var(--white);
  font-size: 14px;
  font-weight: 300;
}
.cabinet .head_tour .icons {
  margin-left: auto;
}
.cabinet .head_tour .icon {
  color: var(--white);
  margin-left: 15px;
  cursor: pointer;
}
.cabinet .cabinet_block {
  width: 100%;
  min-height: 50px;
  padding: 15px;
  border-radius: 15px;
  background: #fdfdfd;
}
.cabinet .block {
  display: flex;
}
.cabinet .block img {
  margin-left: auto;
}
.cabinet h2 {
  color: var(--black);
  font-size: 16px;
  font-weight: 500;
}
.cabinet .link {
  margin-top: 8px;
  color: var(--blue);
  font-size: 14px;
  font-weight: 400;
  display: flex;
  align-items: center;
}
.cabinet .cabinet_block .hr {
  width: 100%;
  height: 1px;
  background: var(--gray);
  margin: 15px 0;
}
.cabinet p {
  color: #888;
  font-size: 12px;
  font-weight: 400;
  line-height: 1;
}
/* .cabinet .page {
  padding-top: 20px;
} */
.cabinet .cabinet_block.two {
  border-radius: 10px;
  padding: 8px;
  background: var(--white);
  position: relative;
  box-shadow: 0px 2px 10px 0px rgba(0, 27, 75, 0.15);
}
.cabinet .cabinet_block.two p {
  width: 90%;
}
.cabinet .cabinet_block.two .icon {
  color: var(--blue);
  top: 30px;
  right: 0;
  position: absolute;
}
.cabinet .cabinet_block h1 {
  color: var(--blue);
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 8px;
}
.cabinet .link_page {
  cursor: pointer;
  color: var(--black);
  font-size: 14px;
  font-weight: 500;
  width: 100%;
  padding: 15px 0;
  display: flex;
  gap: 12px;
  align-items: center;
  background: var(--white);
  border-bottom: 1px solid var(--gray);
  position: relative;
}
.cabinet .link_page img {
  margin-right: 5px;
}
.cabinet .link_page .leng {
  margin-left: auto;
}
.cabinet .leng_text {
  width: 100%;
  padding-bottom: 30px;
}
.cabinet .out_text {
  width: 100%;
  padding-bottom: 30px;
}
.cabinet .out_div,
.cabinet .leng_giv {
  width: 100%;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  border-bottom: 1px solid #ddd;
  border: none;
}
.cabinet .leng_giv {
  padding: 10px 8px;
}
.cabinet .out_div img {
  margin-right: 5px;
}
.cabinet .leng_giv {
  width: 100%;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  cursor: pointer;
  background: var(--white);
  /* border-bottom: 1px solid #ddd; */
}
.cabinet .leng_giv .dot {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--blue);
  margin-right: 0;
}
.cabinet .bonus_price {
  margin-top: 5px;
  font-size: 22px !important;
  font-weight: 400 !important;
  color: var(--black) !important;
}
