.box_header {
  width: 100%;
  height: 60px;
  background: var(--blue);
}
.header {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 60px;
  background: var(--blue);
  z-index: 99;
}
.header_box {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header_box h1 {
  color: var(--unnamed, #fdfdfd) !important;
  /* text-align: center !important; */
  font-size: 17px !important;
  font-weight: 500 !important;
}
.header_box p {
  margin-top: 5px;
  color: var(--unnamed, #fdfdfd);
  text-align: center;
  font-size: 13px;
  font-weight: 300;
}
.header_box .icon {
  cursor: pointer !important;
  color: #fff !important;
}
