.activation {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 30px;
}
.activation form {
  width: 100%;
}
.activation p {
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  color: var(--black);
  line-height: 1;
}
.activation .re_send {
  margin-top: 8px;
  font-size: 14px;
  font-weight: 500;
  color: var(--blue);
  text-align: center;
  cursor: pointer;
}
.input_full_border {
  margin-top: 15px;
  padding: 14px 0;
  border: 1px solid #377aff94 !important;
  border-radius: 4px !important;
}